<template>
  <v-navigation-drawer app color="white" right width="290">
    <v-list subheader two-line class="mt-1">
      <v-list-item>
        <v-list-item-avatar rounded>
          <v-img src="https://cdn.vuetifyjs.com/images/lists/1.jpg"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-subtitle>I'm a Cashier!</v-list-item-subtitle>
          <v-list-item-title>Jelly Grande</v-list-item-title>
        </v-list-item-content>
        <v-list-item-action>
          <v-badge bordered overlap color="red" dot>
            <v-icon>far fa-bell</v-icon>
          </v-badge>
        </v-list-item-action>
      </v-list-item>
    </v-list>
    <strong class="ml-3">Bills</strong>
    <v-list v-for="(bill, i) in bills" :key="i" subheader two-line class="mt-1">
      <v-list-item>
        <v-list-item-avatar rounded color="grey lighten-4">
          <v-img src="right-coffee.png"></v-img>
        </v-list-item-avatar>
        <v-list-item-content>
          <v-list-item-title class="subtitle-2">{{
            bill.coffeeName
          }}</v-list-item-title>
          <v-btn color="#704232" class="small-btn" dark>+</v-btn
          ><v-btn color="#704232" class="small-btn" dark>-</v-btn>
          <v-list-item-subtitle
            >X1

            <v-btn plain color="#704232" small
              >Notes
              <v-icon right>mdi-pencil</v-icon>
            </v-btn>
          </v-list-item-subtitle>
        </v-list-item-content>
        <v-list-item-action class="caption"
          >${{ bill.price }}</v-list-item-action
        >
        <v-btn class="small-btn mt-3" color="#704232" dark>
          <span class="small-btn-item">x</span>
        </v-btn>
      </v-list-item>
    </v-list>
    <v-toolbar color="rgba(0,0,0,0)" flat>
      <strong>Subtotal</strong><v-spacer></v-spacer><span>${{ subTotal }}</span>
    </v-toolbar>
    <v-toolbar color="rgba(0,0,0,0)" flat class="mt-n6">
      <strong>Tax(10%)</strong><v-spacer></v-spacer><span>${{ tax }}</span>
    </v-toolbar>
    <v-divider class="mx-4"></v-divider>
    <v-toolbar color="rgba(0,0,0,0)" flat>
      <strong>Total</strong><v-spacer></v-spacer><span>${{ total }}</span>
    </v-toolbar>
    <strong class="ml-5">Payment Method</strong>
    <v-item-group mandatory class="mt-n1">
      <v-container>
        <v-row justify="center">
          <v-col
            cols="12"
            md="4"
            v-for="(paymentType, i) in paymentTypes"
            :key="i"
          >
            <v-item v-slot="{ active, toggle }">
              <v-card
                color="#F6EFEF"
                :class="active ? 'borderme' : ''"
                class="d-flex align-center rounded-lg"
                dark
                height="70"
                @click="toggle"
                flat
              >
                <v-row>
                  <v-col cols="12" md="12">
                    <v-list-item three-line class="text-center mt-1">
                      <v-list-item-content>
                        <div>
                          <v-icon :color="active ? '#703232' : 'black'">{{
                            paymentType.iconName
                          }}</v-icon>
                        </div>
                        <v-list-item-subtitle
                          :class="active ? 'brown--text' : 'black--text'"
                          class="mt-n2 caption"
                          >{{ paymentType.type }}</v-list-item-subtitle
                        >
                      </v-list-item-content>
                    </v-list-item>
                  </v-col>
                </v-row>
              </v-card>
            </v-item>
          </v-col>
        </v-row>
      </v-container>
    </v-item-group>
    <div class="mx-3 mt-2">
      <v-btn color="#704232" block dark class="widthoutupercase"
        >Print Bills</v-btn
      >
    </div>
  </v-navigation-drawer>
</template>

<script>
export default {
  data() {
    return {
      bills: [
        {
          coffeeName: "Caramel Frappuccino",
          price: 3.69,
        },
        {
          coffeeName: "Mindal Cappuccino",
          price: 3.69,
        },
        {
          coffeeName: "Chocolate Frappuccino",
          price: 3.69,
        },
        {
          coffeeName: "Pepermint Macchiato",
          price: 3.69,
        },
      ],
      paymentTypes: [
        {
          iconName: "fas fa-money-bill-wave",
          type: "Cach",
        },
        {
          iconName: "fas fa-credit-card",
          type: "debit Card",
        },
        {
          iconName: "fas fa-qrcode",
          type: "E-Wallet",
        },
      ],
    };
  },
  computed: {
    subTotal() {
      return this.bills.reduce((acc, value) => acc + value.price, 0);
    },
    tax() {
      return this.subTotal * 0.1;
    },
    total() {
      return this.tax + this.subTotal;
    },
  },
};
</script>

<style>
.v-card.borderme {
  border: 2px solid #704232 !important;
}
.col-12 {
  padding: 5px !important;
}
.v-btn.widthoutupercase {
  text-transform: none !important;
}
.small-btn{
  height: 12px !important;
  min-width: 12px !important;
  padding: 0px !important;
  margin-bottom: auto;
  border: 5px solid #704232;
}
.small-btn-item {
  margin: 2px;
}

</style>
